<template id="">
   <div class="app">
    <div class="newList">
     <!-- <mt-loadmore :top-method="loadTop" :top-all-loaded="allLoaded" ref="loadmore"> -->

     <ul>
      <li class="listBox" v-for="item in newList">
       <div class="imgList">
        <div class="video_mask"></div>
        <h3 class="dotdot line3">{{item.text}}</h3>
        <div class="palyBtn">
         <img src="../../images/playicon_video@3x_dcf536ff.png" alt="" />
        </div>
        <spam class="videoTime">{{  item.videotime | date }}</spam>
        <!-- autoplay -->
        <video :src="item.videouri" :poster="item.image_small"></video>
        <!-- <div class="player">
          <video-player class="video-player vjs-custom-skin" ref="videoPlayer"
                         :playsinline="true" :options="playerOptions"
                         @play="onPlayerPlay($event)"
                         @pause="onPlayerPause($event)">
          </video-player>
        </div> -->

       </div>
       <div class="newlc">
        <div class="newlc-int inticon1">{{item.comment}}</div>
        <div class="newlc-int inticon2">{{item.comment}}</div>
        <div class="newlc-int inticon3">{{item.ding}}</div>
       </div>
     </li>
     </ul>
     <!-- </mt-loadmore> -->
    </div>
   </div>
</template>
<script>
import { Indicator } from 'mint-ui'
import { videoPlayer } from 'vue-video-player'

export default {
  data () {
    return {
      selected: 'video',
      page:1,
      queryLoading: false,
      moreLoading: false,
      allLoaded: false,
      newList:[],
    }
  },

  components: {
    videoPlayer
  },
  methods: {
    handleChange(index) {
      console.log(index)
    },
    getList(page){
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
      this.$https.get("satinApi?type=4&page="+page)
      .then(response => {
          Indicator.close();
          this.newList = response.data.data
          this.newList = this.newList.concat(response.data.data)
          console.log(response.data.data)
        })
    },
    handleTopChange(status) {
        this.topStatus = status;
    },
    loadTop() {
      setTimeout(() => {
        this.page++
        this.$https.get("satinApi?type=4&page="+this.page)
        .then(response => {
            this.newList = response.data.data
            this.newList = this.newList.concat(response.data.data)
            console.log(response.data.data)
          })
        this.$refs.loadmore.onTopLoaded();
      }, 1500);
    },
    onPlayerPlay(player) {
       // alert("play");
     },
     onPlayerPause(player){
       // alert("pause");
     }
    },
    computed: {
      player() {
        return this.$refs.videoPlayer.player
      }
    },
  mounted:function(){
    Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
    });
    this.getList(1)
 }
}
</script>
<style media="screen">
.imgList {position: relative;  overflow: hidden; height: 5.47rem;  background-color: #000;}
.imgList video {width: 100%; height: 100%;}
.palyBtn {position: absolute; left: 50%; top:50%;width: 1.2rem; margin-top: -.6rem; margin-left: -.6rem;height: 1.2rem; }
.palyBtn img {display: block;width: 100%;}
.video_mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 40%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.8)), to(rgba(0, 0, 0, 0)));
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0));
}
.dotdot{
    position: absolute;
    top: 0;
    color: #fff;
    margin: 5px 10px;
    font-size: 14px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .videoTime {position: absolute; bottom: .27rem; right: .4rem; color: #fff;}
</style>



