import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/view/components/HelloWorld'
import index from '@/view/home/index'
import video from '@/view/video/video'
import music from '@/view/music/music'
import my from '@/view/my/my'
import not from '@/view/404'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: '首页',
      meta:{
        keepAlive:true,
      },
      component: index
    },
    {
      path: '/video',
      name: '视频',
      meta:{
        keepAlive:true,
      },
      component: video
    },
    {
      path: '/music',
      name: '发现',
      component: music
    },
    {
      path: '/my',
      name: '我的',
      component: my
    },
    {
      path: '*',
      name: '404',
      component: not
    }
  ]
})



