<style media="screen">
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
  a:hover {text-decoration: none;}
  .iconfont {display:block; text-align: center; font-size: .53rem; padding-bottom: .13rem;}
  .mint-tabbar{background-color: #F7F7FA;}
  .mint-tabbar a {color: #808080}
  .mint-header {background-color: #C20C0C; height: 1.17rem}
  .hei117 { height: 1.17rem}
  .hei50{ height: 50px;}
</style>



