<template id="">
  <div class="app">
    <!-- <mt-swipe :auto="4000"  @change="handleChange">
        <mt-swipe-item v-for="list in banners">
          <a :href="list.url">
            <img :src="list.src" alt="" :title="list.title">
          </a>
        </mt-swipe-item>
    </mt-swipe> -->
    <div class="newList">
      <mt-loadmore :top-method="loadTop" :top-all-loaded="allLoaded" ref="loadmore">
      <ul>
        <li class="listBox" v-for="item in newList">
          <div class="authorInfo">
            <div class="l pic">
              <img class="imgb imgbx" :src="item.profile_image" alt="">
            </div>
            <div class="l info">
              <span class="authorName">{{item.name}}</span>
              <p>{{item.passtime}}</p>
            </div>
          </div>
          <div class="title">
            {{item.text}}
          </div>
          <div class="imgList" v-show="item.image0 == null ? false : true ">
            <div class="imgInfo"><img class="imgbx" :src="item.image0" alt=""></div>
            <!-- <div class="imgInfo"><img class="imgbx" :src="item.image1" alt=""></div>
            <div class="imgInfo"><img class="imgbx" :src="item.image2" alt=""></div> -->
          </div>
          <div class="newlc">
            <div class="newlc-int inticon1">{{item.comment}}</div>
            <div class="newlc-int inticon2">{{item.comment}}</div>
            <div class="newlc-int inticon3">{{item.ding}}</div>
          </div>
        </li>
      </ul>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
import { Indicator } from 'mint-ui'
export default {
  data () {
    return {
      selected: 'index',
      page:1,
      queryLoading: false,
      moreLoading: false,
      allLoaded: false,
      banners:[
        {
          'title':'徐梦圆',
          'url':'',
          'src':'http://p1.music.126.net/3kLdm0uaPT_hoJt0Tb_rDg==/109951163237526448.jpg'
        },
        {
          'title':'徐梦圆2',
          'url':'',
          'src':'http://p1.music.126.net/XHmwSJhkcdRTIF31qXZXdA==/109951163238506254.jpg'
        },
        {
          'title':'徐梦圆3',
          'url':'',
          'src':'http://p1.music.126.net/w1UgAIHwa6z19NkdfU6THA==/109951163236992751.jpg'
        },
        {
          'title':'徐梦圆4',
          'url':'',
          'src':'http://p1.music.126.net/Hh5xT21b7OWEQ8pD02SzxQ==/109951163238509560.jpg'
        }
      ],
      newList:[]
    }
  },
  methods: {
    handleChange(index) {
      console.log(index)
    },
    getList(page){
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
      this.$https.get("satinApi?type=1&page="+page)
      .then(response => {
          Indicator.close();
          this.newList = response.data.data
          this.newList = this.newList.concat(response.data.data)
          console.log(response.data.data)
        })
    },
    handleTopChange(status) {
        this.topStatus = status;
    },
    loadTop() {
        setTimeout(() => {
          this.page++
          this.$https.get("satinApi?type=1&page="+this.page)
          .then(response => {
              this.newList = response.data.data
              this.newList = this.newList.concat(response.data.data)
              console.log(response.data.data)
            })
          this.$refs.loadmore.onTopLoaded();
        }, 1500);
      },
  },
  beforecreate:function(){

  },
  created:function(){

  },
  mounted:function(){
    Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
    });
    this.getList(1)
 }
}
</script>
<style media="screen">

</style>



